@import '../partials/variables';
//Style Five
#atw_style5{
    .atw__title5{
        &:before{
            content: none;
        }
    }
    .atw--single_item{
        .atw_item_top{
            &:hover{
                .atw_overlay_content{
                    visibility: visible;
                    opacity: 1;
                    top: 50%;
                }
            }
            .atw_overlay_content{
                position: absolute;
                left: 40px;
                top: 60%;
                transform: translateY(-50%);
                width: calc(100% - 80px);
                background: #fff;
                height: 170px;
                text-align: center;
                box-shadow:0 10px 10px rgba(84,77,124,0.1);
                visibility: hidden;
                opacity: 0;
                transition: $transition;
                div{
                    position: absolute;
                    width: 100%;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    .atw_product_actions{
                        margin-bottom: 10px;
                        li{
                            padding: 0 2px;
                            a{
                                display: block;
                                height: 40px;
                                padding: 0 9px;
                                border: 1px solid #e4e4ed;
                                background: #fff;
                                padding-top: 12px;
                                color: #363940;
                            }
                        }
                    }
                }
            }
        }
        .atw_item_bottom{
            padding-top: 20px;
            .atw_item_title{
                font-weight: 400;
                a{
                    color: $titleColor;
                    font-size: 16px;
                    line-height: 26px;
                    &:hover{
                        color: $theme1;
                    }
                }
            }
            .atw_item_info{
                margin-top: 8px;
                .atw_rating, .atw_price{
                    display: block;
                }
                .atw_price{
                    color: $theme1;
                    line-height: 42px;
                    del{
                        color: #9192a3;
                        font-weight: 300;
                        font-size: 14px;
                    }
                    .atw_discount_ratio{
                        border: 1px solid $theme1;
                        font-size: 12px;
                        color: $theme1;
                        line-height: 20px;
                        padding: 0 7px;
                        border-radius: 25px;
                        margin-left: 4px;
                        float: right;
                        margin-top: 10px;
                    }
                }
            }
        }
    }
    .atw_slider_controls{
        .slider--control5{
            border: 1px solid $borderColor;
            border-radius: 0;
            z-index: 2;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            transition: $transition;
            &:hover{
                border-color: $theme1;
                color: $theme1;
            }
        }
        .icon-arrow-left{
            left: 0;
        }
        .icon-arrow-right{
            right: 0;
        }
    }
}